:root {
  --nextgen-popover-content-width: 200px;
  --active-orange-400: #ef6c0f;
  --everyday-blue-100: #d8efef;
  --everyday-blue-200: #a6fff8;
  --everyday-blue-500: #00a4a6;
  --everyday-blue-600: #09757a;
  --everyday-blue-700: #004249;
  --reliable-red-400: #db1802;
  --wp-admin-theme-color: var(--everyday-blue-600) !important;
  --wp-admin-theme-color-darker-10: var(--everyday-blue-600) !important;
  --wp-admin-theme-color-darker-20: var(--everyday-blue-700) !important;
}

