.nextgen-menu-icon {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nextgen-menu-icon--add {
  margin-top: 3px;
}
.nextgen-menu-icon__icon {
  height: 24px;
  margin-bottom: 0;
}
@media (min-width: 400px) {
  .nextgen-menu-icon__icon {
    margin-bottom: 4px;
  }
}
@media (min-width: 782px) {
  .nextgen-menu-icon__icon {
    margin-bottom: 0;
  }
}
.nextgen-menu-icon__label {
  display: none;
}
@media (min-width: 400px) {
  .nextgen-menu-icon__label {
    display: block;
    font-size: 12px;
    font-weight: 400;
  }
}
@media (min-width: 782px) {
  .nextgen-menu-icon__label {
    display: none;
  }
}

