.nextgen-modal {
  border-width: 0;
  box-shadow: none;
}
.nextgen-modal .components-modal__header {
  border-bottom: 0;
  height: 50px;
  margin: 0 -60px;
}
.nextgen-modal .components-modal__content {
  overflow-x: hidden;
  padding: 0 48px 30px;
  text-align: center;
}
.nextgen-modal__title {
  color: #1e1e1e;
  font-size: 1.15rem;
  margin-bottom: 1em;
  margin-top: 3px;
}
.nextgen-modal__subtitle {
  font-size: 1.1rem;
  font-weight: 300;
  margin-bottom: 1rem;
  margin-top: 2rem;
}
.nextgen-modal__warning {
  background-color: #f0f0f0;
  margin: 1.5em 0;
  position: relative;
}
.nextgen-modal__warning > p {
  padding: 1em 1em 1em 4em;
}
.nextgen-modal__warning > svg {
  fill: var(--active-orange-400);
  left: 1.5em;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.nextgen-modal p {
  font-size: 1.1em;
}
.nextgen-modal .components-button.is-primary {
  background-color: #1e1e1e;
  border-radius: 0;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  font-weight: 600;
  height: 48px;
  line-height: 1;
  padding-left: 1.5em;
  padding-right: 1.5em;
  transition: background-color 0.2s;
}
.nextgen-modal .components-button.is-primary:disabled {
  color: #fff;
  opacity: 0.5;
  pointer-events: none;
}
.nextgen-modal .components-button.is-link {
  color: var(--wp-admin-theme-color);
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-size: 16px;
  padding: 1em;
  text-decoration: none;
}
.nextgen-modal .components-button.is-link:hover {
  color: #1e1e1e;
}

.nextgen-tooltip.nextgen-tooltip {
  background-color: #1e1e1e;
  font-size: 0.9rem;
  padding: 2em;
  text-align: left;
}
.nextgen-tooltip.nextgen-tooltip.show {
  opacity: 1;
}
.nextgen-tooltip.nextgen-tooltip.place-right::before, .nextgen-tooltip.nextgen-tooltip.place-right::after, .nextgen-tooltip.nextgen-tooltip.place-left::before, .nextgen-tooltip.nextgen-tooltip.place-left::after {
  border-bottom-width: 8px;
  border-top-width: 8px;
  margin-top: 0;
  top: 18%;
}
.nextgen-tooltip.nextgen-tooltip.place-right::before, .nextgen-tooltip.nextgen-tooltip.place-right::after {
  border-right-color: #1e1e1e;
  border-right-width: 10px;
  left: -10px;
}
.nextgen-tooltip.nextgen-tooltip.place-left::before, .nextgen-tooltip.nextgen-tooltip.place-left::after {
  border-left-color: #1e1e1e;
  border-left-width: 10px;
  right: -10px;
}
.nextgen-tooltip .nextgen-tooltip__title {
  font-size: 1.25em;
  font-weight: 400;
  margin: 0 0 0.75em;
}
.nextgen-tooltip.nextgen-tooltip .nextgen-tooltip__type {
  font-size: 0.9em;
  margin-bottom: 1em;
  text-transform: uppercase;
}
.nextgen-tooltip.nextgen-tooltip .nextgen-tooltip__type--info {
  color: var(--everyday-blue-200);
  fill: var(--everyday-blue-200);
}
.nextgen-tooltip.nextgen-tooltip .nextgen-tooltip__type > svg {
  margin-left: -2px;
  margin-right: 0.3em !important;
  margin-top: -5px;
  vertical-align: middle;
}